import { useTheme } from '@emotion/react';
import { ReactComponent as GlobeIcon } from 'assets/images/globe.svg';
import { TOP_NAV_HEIGHT_REM } from 'layout/Header';
import * as React from 'react';
import { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useDispatch } from 'react-redux';
// import GameFlag from './ui/GameFlag';
// import countriesData from 'data/countries.json';
import { setInWiki } from 'store/actions';

import ArrowCircleLeftIcon from '@mui/icons-material/ArrowCircleLeft';
import { Box, Stack, SvgIcon, Typography, Zoom, Fade } from '@mui/material';

import NewGame from './NewGame';
import FunTypo from './ui/FunTypo';
import MainButton from './ui/MainButton';
import { pxToRem } from 'util/util';

const SCREEN_HOME = 0;
const SCREEN_GAME_OPTIONS = 1;
// const SCREEN_WIKI = 2;

const Home = () => {
    const [screen, setScreen] = useState(0);
    const theme = useTheme();
    const dispatch = useDispatch();
    const { t } = useTranslation();

    const handleClickNewGame = () => {
        setScreen(SCREEN_GAME_OPTIONS);
    };
    const handleClickWiki = () => {
        dispatch(setInWiki(true));
    };

    const handleClickHome = () => {
        setScreen(SCREEN_HOME);
    };

    const showHome = screen === SCREEN_HOME;
    // const showGameOptions = screen === SCREEN_GAME_OPTIONS;

    return (
        <>
            <Stack
                height="100%"
                flexGrow={1}
				position="relative"
                sx={
                    {
                        // background: "#000",
                    }
                }
            >
				<Box
					position="absolute"
					margin="0 auto"
					width="100%"
					top={showHome ? 0 : `calc(-${TOP_NAV_HEIGHT_REM}/2)`}
					overflow="hidden"
					sx={{
						height: `calc(${showHome ? 30 : 20}vh - ${TOP_NAV_HEIGHT_REM})`,
						minHeight: pxToRem(showHome ? 220 : 150),
						transition: 'all 0.25s ease',
						pointerEvents: 'none',
					}}
				>
					<Typography variant="h1" display="block">
						<FunTypo
							text={t('Country')}
							color={theme.palette.text.title.replace('#', '')}
							stroke={true}
							strokeWidth={pxToRem(2)}
							distance={pxToRem(6 * (showHome ? 1 : 0.70))}
							sx={{
								display: 'block',
								fontSize: pxToRem(108 * (showHome ? 1 : 0.70)),
								lineHeight: pxToRem(110 * (showHome ? 1 : 0.70)),
								fontWeight: 800,
								textAlign: 'center',
								mb: pxToRem(-40 * (showHome ? 1 : 0.70)),
								transition: '0.25s ease',
							}}
						/>
						<FunTypo
							text={t('Quiz')}
							color={theme.palette.text.title.replace('#', '')}
							stroke={false}
							strokeWidth={pxToRem(2)}
							distance={pxToRem(7 * (showHome ? 1 : 0.70))}
							sx={{
								display: 'block',
								fontSize: pxToRem(124 * (showHome ? 1 : 0.70)),
								lineHeight: pxToRem(132 * (showHome ? 1 : 0.70)),
								fontWeight: 800,
								textAlign: 'center',
								transition: '0.25s ease',
							}}
						/>
					</Typography>
				</Box>
				{showHome && (
					<Box
						width="100%"
						height={pxToRem(240)}
						position="absolute"
						top="calc(50% - 120px)"
					>
						<SvgIcon
							sx={{
								height: pxToRem(240),
								width: pxToRem(240),
								display: 'block',
								margin: '0 auto',
								color:
									theme.palette.mode === 'dark'
										? theme.palette.primary.light
										: theme.palette.secondary.main,
								// color: purple.darkest,
								opacity: theme.palette.mode === 'dark' ? 0.5 : 1,
							}}
						>
							<GlobeIcon />
						</SvgIcon>
					</Box>
				)}
				{!showHome && (
					<Box
						display="flex"
						alignItems="center"
						flexDirection="column"
						justifyContent="flex-start"
						width="100%"
						position="absolute"
						sx={{
							height: '69vh',
							top: showHome
								? 'calc(40vh - ' + TOP_NAV_HEIGHT_REM + ')'
								: 'calc(20vh - ' + TOP_NAV_HEIGHT_REM + ')',
						}}
					>
						<NewGame cssTransition={{
							// display: showHome ? 'none' : 'flex',
							// opacity: showHome ? 0 : 1,
							// transition: 'opacity 2s 2s ease',
						}} />
					</Box>
				)}
                <Box
                    display="flex"
                    alignItems="center"
                    flexDirection="column"
                    justifyContent="flex-start"
					width="100%"
					position="absolute"
					bottom="0"
                    my={4}
                    sx={{
                        height: showHome ? '25vh' : '6vh',
                        // transition: 'height 0.25s ease',
                    }}
                >
                    {showHome ? (
                        <Stack spacing={2}>
                            <MainButton
                                buttonP={{
                                    onClick: handleClickNewGame,
                                }}
                            >
                                {t('Play')}
                            </MainButton>
                            <MainButton
                                buttonP={{
                                    color: 'success',
                                    onClick: handleClickWiki,
                                }}
                            >
                                {t('Wiki')}
                            </MainButton>
                        </Stack>
                    ) : (
                        <MainButton
                            buttonP={{
                                color: 'success',
                                onClick: handleClickHome,
                                startIcon: <ArrowCircleLeftIcon />,
                            }}
                        >
                            {t('Home')}
                        </MainButton>
                    )}
                </Box>
            </Stack>
        </>
    );
};

export default Home;
