import { useTheme } from '@emotion/react';
import * as React from 'react';

import { LinearProgress, Stack, Typography } from '@mui/material';

import FunTypo from '../ui/FunTypo';
import { pxToRem } from 'util/util';

const GameProgress = (props) => {
    const theme = useTheme();

    const { game } = props;

    return (
        <Stack
            display="flex"
            alignItems="center"
            direction="row"
            justifyContent="space-between"
            sx={{
                my: 2,
                width: '100%',
            }}
        >
            <Stack
                display="flex"
                alignItems="center"
                direction="row"
                sx={{
                    width: 'auto',
                }}
            >
                <Typography fontSize={pxToRem(18)} textAlign="right" fontWeight="700">
                    {game.currentTurn + 1 + ' / ' + game.questions.length}
                </Typography>
            </Stack>
            <LinearProgress
                color="success"
                variant="buffer"
                value={((game.currentTurn+1) / game.questions.length) * 100}
				valueBuffer={((game.currentScore) / game.questions.length) * 100}
                sx={{
                    flexGrow: 1,
                    marginRight: pxToRem(16),
                    marginLeft: pxToRem(16),
                }}
            />
            <Stack
                display="flex"
                alignItems="center"
                direction="row"
                sx={{
                    width: 'auto',
                }}
            >
                <FunTypo
                    text={game.currentScore}
                    color={theme.palette.text.main.replace('#', '')}
                    stroke={false}
                    distance={pxToRem(2)}
                    flexGrow={0}
                    sx={{
                        display: 'block',
                        fontSize: pxToRem(20),
                        lineHeight: pxToRem(24),
                        fontWeight: 800,
                        width: 'auto',
                    }}
                />
            </Stack>
        </Stack>
    );
};

export default GameProgress;
