import { useTheme } from '@emotion/react';
import MainButton from 'components/ui/MainButton';
import * as React from 'react';

import CancelIcon from '@mui/icons-material/Cancel';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import { SvgIcon } from '@mui/material';
import { pxToRem } from 'util/util';

const GameButton = (props) => {
    const {
        // text,
        // colorEffect = '000',
        // // stroke = false,
        // distance = pxToRem(3),
        // patternZoom = 2,
        onClick,
        phase,
        choice,
        rightAnswer,
        chosenAnswer,
        isFlag,
        answerAdditionnalText,
    } = props;
    const theme = useTheme();

    let color = 'primary';
    let icon = undefined;
    if (phase === 2) {
        if (choice === rightAnswer && chosenAnswer === choice) {
            color = 'success';
            icon = <CheckCircleIcon />;
        } else if (choice === rightAnswer) {
            color = 'secondary';
        } else if (choice === chosenAnswer) {
            color = 'error';
            icon = <CancelIcon />;
        }
    }

    return (
        <MainButton
            buttonP={{
                color: color,
                onClick: onClick,
                variant: isFlag ? 'outlined' : 'contained',
                // variant: 'outlined',
                // variant: isFlag || (phase === 1 && choice !== rightAnswer && chosenAnswer !== choice) ? 'outlined' : 'contained',
                sx: {
                    position: 'relative',
                    ...(color === 'secondary' || color === 'success'
                        ? {
                              outlineOffset: pxToRem(3),
                              outline: `2px solid ${color === 'success' ? theme.palette[color].main : theme.palette.background.rightAnswer}`,
                          }
                        : {}),
                    // ...(phase === 0 ? {
                    // backgroundColor: "#fff",
                    // '&:hover': {
                    // 	backgroundColor: "rgba(255,255,255,0.5)",
                    // },
                    // } : {}),
                    textAlign: 'center',
                    // width: isFlag ? `calc(50% - ${pxToRem(8)})` : '100%',
					width: '100%',
                    ...(isFlag ? { minHeight: '126px', backgroundColor: theme.palette.neutral[50]} : {}),
                    padding: pxToRem(10),

                    // boxShadow: `2px 6px #${colorEffect}`,

                    /* '&:after': {
						pointerEvents: "none",
						content: '""',
						background: 'url(\'data:image/svg+xml;utf8,<svg viewBox="0 0 4 4" width="'+(4*patternZoom)+'px" height="'+(4*patternZoom)+'px"><defs></defs><polygon points="0 0 4 4 4 2 2 0" fill="%23'+colorEffect+'"></polygon><polygon points="0 4 2 4 0 2" fill="%23'+colorEffect+'"></polygon></svg>\') repeat',
						// background: 'url(\'data:image/svg+xml;utf8,<svg viewBox="0 0 6 4" width="'+(6*patternZoom)+'px" height="'+(4*patternZoom)+'px"><defs></defs><polygon points="0 0 6 4 6 3 1.5 0" fill="%23'+colorEffect+'"></polygon><polygon points="0 4 1.5 4 0 3" fill="%23'+colorEffect+'"></polygon></svg>\') repeat',
						display: "block",
						height: "100%",
						width: "100%",
						position: "absolute",
						left: "calc(50% + "+distance+")",
						top: "calc(50% + "+distance+")",
						transform: "translate(-50%, -50%)",
						borderRadius: "12px",
						zIndex: -1,
					} */
                },
                ...(!isFlag && icon
                    ? {
                          endIcon: <SvgIcon fontSize={pxToRem(20)}>{icon}</SvgIcon>,
                      }
                    : {}),
            }}
            typoP={{
                fontSize: pxToRem(20),
                fontWeight: '700',
                lineHeight: pxToRem(26),
            }}
            isFlag={isFlag}
        >
            {props.children}
            {choice === rightAnswer && phase === 2 && answerAdditionnalText ? (
                <>
                    <br />
                    <sub>{answerAdditionnalText}</sub>
                </>
            ) : null}
            {isFlag && icon && (
                <SvgIcon
                    fontSize="large"
                    sx={{
                        position: 'absolute',
                        right: pxToRem(8),
                    }}
                >
                    {icon}
                </SvgIcon>
            )}
        </MainButton>
    );
};

export default GameButton;
