import { common } from '@mui/material/colors';
import { alpha } from '@mui/material/styles';

import { error, info, neutral, purple, secondary, success, warning } from './colors';

export function createPalette() {
    return {
        action: {
            active: neutral[500],
            disabled: alpha(neutral[900], 0.38),
            disabledSelected: alpha(neutral[900], 0.56),
            disabledBackground: alpha(neutral[900], 0.12),
            focus: alpha(neutral[900], 0.16),
            hover: alpha(neutral[900], 0.04),
            selected: alpha(neutral[900], 0.12),
        },
        background: {
            default: secondary.lightest,
            paper: neutral[50],
            tile: neutral[50],
            tileHover: purple.alpha12,
			semi: neutral[50],
			rightAnswer: secondary.main,
        },
        divider: neutral[100],
        error,
        info,
        mode: 'light',
        neutral,
        primary: {...purple, lightContrast: common.white},
        secondary: {...secondary, lightContrast: common.white},
        success,
        text: {
            title: common.black,
            main: neutral[700],
            primary: neutral[700],
            secondary: neutral[500],
            disabled: alpha(neutral[900], 0.38),
        },
        warning,
        shadow: neutral[1000],
    };
}
