import { common } from '@mui/material/colors';
import { alpha } from '@mui/material/styles';

import { error, info, neutral, purple, secondary, success, warning } from '../colors';

const neutralConversion = {
    50: 1100,
    100: 1000,
    200: 900,
    300: 500,
    400: 500,
    450: 600,
    500: 500,
    600: 450,
    700: 400,
    800: 300,
    900: 200,
    1000: 100,
    1100: 50,
};

const neutralDark = {};
Object.keys(neutral).forEach((key) => {
    neutralDark[key] = neutral[neutralConversion[key]];
});

export function createPalette() {
    return {
        action: {
            active: neutralDark[700],
            disabled: alpha(neutralDark[900], 0.38),
            disabledBackground: alpha(neutralDark[900], 0.12),
            focus: alpha(neutralDark[900], 0.16),
            hover: alpha(neutralDark[900], 0.04),
            selected: alpha(neutralDark[900], 0.12),
        },
        background: {
            default: neutralDark[50],
            paper: neutralDark[100],
            tile: neutralDark[200],
            tileHover: purple.alpha50,
			semi: neutralDark[100],
			rightAnswer: purple.main,
        },
        divider: neutralDark[300],
        error,
        info,
        mode: 'dark',
        neutral: neutralDark,
        primary: {...purple, contrast: common.white},
        secondary: {...secondary, contrast: common.white},
        success,
        text: {
            title: common.white,
            main: neutralDark[1000],
            primary: neutralDark[1000],
            secondary: neutralDark[600],
            disabled: alpha(neutralDark[1000], 0.38),
        },
        warning,
        shadow: neutral[1000],
    };
}
